export const circleClockwise = (originObj, radius) => "M "+(originObj.x+radius)+","+originObj.y +" m 0,0  a "+radius+","+radius+" 0 0 1 "+(-radius * 2)+",0 a "+radius+","+radius+" 0 0 1 "+(radius * 2)+",0" 
export const circleAntiClockwise = (originObj, radius) => "M "+(originObj.x-radius)+","+originObj.y +" m 0,0  a "+radius+","+radius+" 0 1 0 "+(radius * 2)+",0 a "+radius+","+radius+" 0 1 0 "+(-radius * 2)+",0" 

export const triangle = (radius, direction = 'up') => {
    const triRadius = radius * 0.8
    return direction === 'up' 
        ? `M-${triRadius}, ${triRadius * 0.5}  L0, -${triRadius}  L${triRadius}, ${triRadius * 0.5}z` // Up triangle
        : `M-${triRadius}, -${triRadius * 0.5} L0, ${triRadius}  L${triRadius}, -${triRadius * 0.5}z`  // Down triangle
}   


export const arrowhead = (radius, direction = 'up') => {
    const triRadius = radius * 0.8
    return direction === 'up' 
        ? `M-${triRadius}, ${triRadius * 0.5}  L0, -${triRadius}  L${triRadius}, ${triRadius * 0.5}` // Up triangle
        : `M-${triRadius}, -${triRadius * 0.5} L0, ${triRadius}  L${triRadius}, -${triRadius * 0.5}`  // Down triangle
}  